"use client";

import React from 'react';
import { Card } from "@/components/ui/card";
import { Select } from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart";
import { LineChart, Line, ResponsiveContainer } from 'recharts';

interface TechnicalData {
  timestamp: string;
  price: number;
  ema9: number;
  ema20: number;
  volume: number;
  rsi: number;
}

interface StockAnalysis {
  symbol: string;
  data: TechnicalData[];
  patterns: {
    name: string;
    confidence: number;
    signal: 'bullish' | 'bearish' | 'neutral';
  }[];
}

export default function TechnicalAnalysis() {
  const [selectedStock, setSelectedStock] = React.useState<string>("NIFTY");
  const [timeframe, setTimeframe] = React.useState<string>("1D");
  const [analysis, setAnalysis] = React.useState<StockAnalysis | null>(null);

  // Mock data - Replace with real API integration
  React.useEffect(() => {
    const mockAnalysis: StockAnalysis = {
      symbol: "NIFTY",
      data: Array.from({ length: 100 }, (_, i) => ({
        timestamp: new Date(Date.now() - (100 - i) * 300000).toISOString(),
        price: 19200 + Math.random() * 200,
        ema9: 19250 + Math.random() * 100,
        ema20: 19200 + Math.random() * 100,
        volume: 1000000 + Math.random() * 500000,
        rsi: 30 + Math.random() * 40
      })),
      patterns: [
        {
          name: "Double Bottom",
          confidence: 85,
          signal: "bullish"
        },
        {
          name: "EMA Crossover",
          confidence: 75,
          signal: "bullish"
        }
      ]
    };

    setAnalysis(mockAnalysis);
  }, [selectedStock, timeframe]);

  const chartConfig = {
    price: {
      label: "Price",
      color: "#0ea5e9"
    },
    ema9: {
      label: "EMA9",
      color: "#22c55e"
    },
    ema20: {
      label: "EMA20",
      color: "#ef4444"
    }
  };

  const getSignalColor = (signal: 'bullish' | 'bearish' | 'neutral') => {
    switch (signal) {
      case 'bullish':
        return 'bg-green-500';
      case 'bearish':
        return 'bg-red-500';
      default:
        return 'bg-yellow-500';
    }
  };

  if (!analysis) {
    return <div className="flex justify-center items-center h-96">Loading...</div>;
  }

  return (
    <div className="space-y-6">
      {/* Controls */}
      <Card className="p-4">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-4">
            <Select defaultValue={selectedStock} onValueChange={setSelectedStock}>
              <option value="NIFTY">NIFTY</option>
              <option value="BANKNIFTY">BANKNIFTY</option>
              <option value="RELIANCE">RELIANCE</option>
            </Select>
            <div className="flex gap-2">
              <Button
                variant={timeframe === "1D" ? "default" : "outline"}
                onClick={() => setTimeframe("1D")}
              >
                1D
              </Button>
              <Button
                variant={timeframe === "1W" ? "default" : "outline"}
                onClick={() => setTimeframe("1W")}
              >
                1W
              </Button>
              <Button
                variant={timeframe === "1M" ? "default" : "outline"}
                onClick={() => setTimeframe("1M")}
              >
                1M
              </Button>
            </div>
          </div>
          <Button>Refresh</Button>
        </div>

        {/* Main Chart */}
        <div className="h-[400px] mb-6">
          <ChartContainer config={chartConfig}>
            <ResponsiveContainer width="100%" height={400}>
              <LineChart data={analysis.data}>
                <Line
                  type="monotone"
                  dataKey="price"
                  stroke="var(--color-price)"
                  strokeWidth={2}
                  dot={false}
                />
                <Line
                  type="monotone"
                  dataKey="ema9"
                  stroke="var(--color-ema9)"
                  strokeWidth={1.5}
                  dot={false}
                />
                <Line
                  type="monotone"
                  dataKey="ema20"
                  stroke="var(--color-ema20)"
                  strokeWidth={1.5}
                  dot={false}
                />
                <ChartTooltip
                  content={({ active, payload }) => (
                    <ChartTooltipContent
                      active={active}
                      payload={payload}
                      labelKey="timestamp"
                    />
                  )}
                />
              </LineChart>
            </ResponsiveContainer>
          </ChartContainer>
        </div>

        {/* Indicators */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <Card className="p-3">
            <h3 className="text-sm font-medium mb-2">RSI (14)</h3>
            <div className="text-2xl font-bold">
              {analysis.data[analysis.data.length - 1].rsi.toFixed(2)}
            </div>
            <div className="text-sm text-gray-500">
              {analysis.data[analysis.data.length - 1].rsi > 70 
                ? "Overbought" 
                : analysis.data[analysis.data.length - 1].rsi < 30 
                ? "Oversold" 
                : "Neutral"}
            </div>
          </Card>
          
          <Card className="p-3">
            <h3 className="text-sm font-medium mb-2">Volume Analysis</h3>
            <div className="text-2xl font-bold">
              {(analysis.data[analysis.data.length - 1].volume / 1000000).toFixed(2)}M
            </div>
            <div className="text-sm text-gray-500">
              {analysis.data[analysis.data.length - 1].volume > 1500000 
                ? "High Volume" 
                : "Normal Volume"}
            </div>
          </Card>

          <Card className="p-3">
            <h3 className="text-sm font-medium mb-2">EMA Cross</h3>
            <div className="text-2xl font-bold">
              {analysis.data[analysis.data.length - 1].ema9 > 
               analysis.data[analysis.data.length - 1].ema20 
                ? "Bullish" 
                : "Bearish"}
            </div>
            <div className="text-sm text-gray-500">
              EMA9/EMA20 Cross
            </div>
          </Card>
        </div>

        {/* Pattern Recognition */}
        <div className="mt-6">
          <h3 className="text-lg font-semibold mb-3">Pattern Recognition</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {analysis.patterns.map((pattern, index) => (
              <Card key={index} className="p-3">
                <div className="flex justify-between items-center">
                  <div>
                    <h4 className="font-medium">{pattern.name}</h4>
                    <div className="text-sm text-gray-500">
                      Confidence: {pattern.confidence}%
                    </div>
                  </div>
                  <Badge className={getSignalColor(pattern.signal)}>
                    {pattern.signal.toUpperCase()}
                  </Badge>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </Card>
    </div>
  );
}
