"use client";

import React from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Card } from "@/components/ui/card";
import { fetchOptionChain, type OptionChainData } from '@/lib/api/option-chain-service';

interface OptionChainProps {
  symbol: string;
  onOptionSelect?: (strike: number, type: 'CE' | 'PE') => void;
  filterCallOptions?: boolean;
}

export function OptionChain({ symbol, onOptionSelect, filterCallOptions = false }: OptionChainProps) {
  const [optionChain, setOptionChain] = React.useState<OptionChainData[]>([]);
  const [selectedExpiry, setSelectedExpiry] = React.useState<string>('');
  const [expiryDates, setExpiryDates] = React.useState<string[]>([]);
  const [spotPrice, setSpotPrice] = React.useState<number>(0);

  React.useEffect(() => {
    const loadOptionChain = async () => {
      if (!symbol) return;
      
      const response = await fetchOptionChain(symbol, selectedExpiry);
      setOptionChain(response.data);
      setExpiryDates(response.expiryDates);
      setSpotPrice(response.spotPrice);
      if (!selectedExpiry && response.expiryDates.length > 0) {
        setSelectedExpiry(response.expiryDates[0]);
      }
    };

    loadOptionChain();
  }, [symbol, selectedExpiry]);

  const formatNumber = (num: number) => {
    return new Intl.NumberFormat('en-IN', {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2
    }).format(num);
  };

  const getRowStyle = (strikePrice: number) => {
    if (strikePrice === spotPrice) return "bg-gray-100";
    if (strikePrice < spotPrice) return "bg-red-50";
    return "bg-green-50";
  };

  return (
    <Card className="p-4">
      <div className="flex justify-between items-center mb-4">
        <h3 className="text-lg font-semibold">Option Chain - {symbol}</h3>
        <div className="flex items-center gap-4">
          <span className="text-sm font-medium">Spot: {formatNumber(spotPrice)}</span>
          <Select value={selectedExpiry} onValueChange={setSelectedExpiry}>
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Select expiry" />
            </SelectTrigger>
            <SelectContent>
              {expiryDates.map(date => (
                <SelectItem key={date} value={date}>
                  {new Date(date).toLocaleDateString()}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <div className="overflow-x-auto">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead colSpan={5} className="text-center bg-blue-50">CALLS</TableHead>
              <TableHead className="text-center">Strike</TableHead>
              {!filterCallOptions && (
                <TableHead colSpan={5} className="text-center bg-blue-50">PUTS</TableHead>
              )}
            </TableRow>
            <TableRow>
              <TableHead className="text-right">LTP</TableHead>
              <TableHead className="text-right">Chg%</TableHead>
              <TableHead className="text-right">OI</TableHead>
              <TableHead className="text-right">Volume</TableHead>
              <TableHead className="text-right">IV</TableHead>
              <TableHead className="text-center font-bold">Strike</TableHead>
              {!filterCallOptions && (
                <>
                  <TableHead className="text-right">LTP</TableHead>
                  <TableHead className="text-right">Chg%</TableHead>
                  <TableHead className="text-right">OI</TableHead>
                  <TableHead className="text-right">Volume</TableHead>
                  <TableHead className="text-right">IV</TableHead>
                </>
              )}
            </TableRow>
          </TableHeader>
          <TableBody>
            {optionChain.map((row) => (
              <TableRow key={row.strikePrice} className={getRowStyle(row.strikePrice)}>
                <TableCell 
                  className="text-right cursor-pointer hover:bg-blue-100"
                  onClick={() => onOptionSelect?.(row.strikePrice, 'CE')}
                >
                  {formatNumber(row.callOption.lastPrice)}
                </TableCell>
                <TableCell className="text-right">{formatNumber(row.callOption.change)}</TableCell>
                <TableCell className="text-right">{row.callOption.openInterest.toLocaleString()}</TableCell>
                <TableCell className="text-right">{row.callOption.volume.toLocaleString()}</TableCell>
                <TableCell className="text-right">{formatNumber(row.callOption.impliedVolatility)}%</TableCell>
                <TableCell className="text-center font-bold">{formatNumber(row.strikePrice)}</TableCell>
                {!filterCallOptions && (
                  <>
                    <TableCell 
                      className="text-right cursor-pointer hover:bg-blue-100"
                      onClick={() => onOptionSelect?.(row.strikePrice, 'PE')}
                    >
                      {formatNumber(row.putOption.lastPrice)}
                    </TableCell>
                    <TableCell className="text-right">{formatNumber(row.putOption.change)}</TableCell>
                    <TableCell className="text-right">{row.putOption.openInterest.toLocaleString()}</TableCell>
                    <TableCell className="text-right">{row.putOption.volume.toLocaleString()}</TableCell>
                    <TableCell className="text-right">{formatNumber(row.putOption.impliedVolatility)}%</TableCell>
                  </>
                )}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    </Card>
  );
}
