import { TechnicalIndicators, fetchHistoricalData, evaluateSignal, SignalCriteria } from './technical-analysis-service';

export interface OptionGreeks {
  delta: number;
  gamma: number;
  theta: number;
  vega: number;
  rho?: number;
}

export interface OptionData {
  lastPrice: number;
  change: number;
  openInterest: number;
  volume: number;
  impliedVolatility: number;
  bidPrice: number;
  askPrice: number;
  underlyingPrice: number;
  strikePrice: number;
  expiryDate: string;
  greeks: OptionGreeks;
  technicalIndicators?: TechnicalIndicators;
  signal?: boolean;
}

export interface OptionChainData {
  strikePrice: number;
  expiryDate: string;
  callOption: OptionData;
  putOption: OptionData;
}

export interface OptionChainResponse {
  underlying: string;
  spotPrice: number;
  timestamp: string;
  expiryDates: string[];
  data: OptionChainData[];
}

// Mock data for demonstration
const defaultSignalCriteria: SignalCriteria = {
  minRSI: 30,
  maxRSI: 70,
  volumeMultiplier: 1.5,
  trendEMA: 'above',
  macdCrossover: true,
  atrMultiplier: 0.02 // 2% volatility threshold
};

const generateMockOptionChain = async (
  underlying: string, 
  spotPrice: number,
  signalCriteria: SignalCriteria = defaultSignalCriteria
): Promise<OptionChainResponse> => {
  const now = new Date();
  const expiryDates = [
    new Date(now.getFullYear(), now.getMonth(), now.getDate() + 7),
    new Date(now.getFullYear(), now.getMonth(), now.getDate() + 14),
    new Date(now.getFullYear(), now.getMonth() + 1, 1),
  ].map(date => date.toISOString().split('T')[0]);

  const strikes = Array.from({ length: 11 }, (_, i) => 
    spotPrice + ((i - 5) * (spotPrice * 0.01))
  );

  // Fetch historical data for technical analysis
  const historicalData = await fetchHistoricalData(underlying);
  
  const data: OptionChainData[] = await Promise.all(strikes.map(async (strike) => {
    const generateOptionData = async (isCall: boolean): Promise<OptionData> => {
      const lastPrice = Math.random() * 100;
      const data = {
        lastPrice,
        change: (Math.random() - 0.5) * 10,
        openInterest: Math.floor(Math.random() * 10000),
        volume: Math.floor(Math.random() * 5000),
        impliedVolatility: Math.random() * 50,
        bidPrice: lastPrice - Math.random() * 2,
        askPrice: lastPrice + Math.random() * 2,
        underlyingPrice: spotPrice,
        strikePrice: strike,
        expiryDate: expiryDates[0],
        greeks: {
          delta: isCall ? Math.random() : -Math.random(),
          gamma: Math.random() * 0.1,
          theta: -Math.random() * 10,
          vega: Math.random() * 10,
          rho: Math.random() * 5
        }
      };

      // Add technical indicators and signal evaluation
      const technicalIndicators = {
        ema20: Math.random() * 100,
        ema50: Math.random() * 100,
        rsi14: Math.random() * 100,
        atr14: Math.random() * 10,
        macd: {
          macd: Math.random() * 10,
          signal: Math.random() * 10,
          histogram: Math.random() * 5
        },
        volume: data.volume,
        volumeSMA20: Math.random() * 5000
      };

      return {
        ...data,
        technicalIndicators,
        signal: evaluateSignal(historicalData, technicalIndicators, signalCriteria)
      };
    };

    return {
      strikePrice: Math.round(strike),
      expiryDate: expiryDates[0],
      callOption: await generateOptionData(true),
      putOption: await generateOptionData(false)
    };
  }));

  return {
    underlying,
    spotPrice,
    timestamp: new Date().toISOString(),
    expiryDates,
    data
  };
};

export const fetchOptionChain = async (
  symbol: string,
  expiry?: string,
  signalCriteria?: SignalCriteria
): Promise<OptionChainResponse> => {
  // In a real implementation, this would make an API call to fetch live data
  const spotPrices: Record<string, number> = {
    'NIFTY': 19500,
    'SENSEX': 65000,
    'BANKNIFTY': 44000
  };

  return generateMockOptionChain(symbol, spotPrices[symbol] || 1000, signalCriteria);
};

