export interface OHLCV {
  timestamp: number;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
}

export interface TechnicalIndicators {
  ema20: number;
  ema50: number;
  rsi14: number;
  atr14: number;
  macd: {
    macd: number;
    signal: number;
    histogram: number;
  };
  volume: number;
  volumeSMA20: number;
}

export interface SignalCriteria {
  minRSI?: number;
  maxRSI?: number;
  minVolume?: number;
  volumeMultiplier?: number;
  trendEMA?: 'above' | 'below';
  macdCrossover?: boolean;
  atrMultiplier?: number;
}

export function calculateEMA(prices: number[], period: number): number[] {
  const ema = [];
  const multiplier = 2 / (period + 1);

  // Start with SMA for the first EMA value
  let sma = prices.slice(0, period).reduce((sum, price) => sum + price, 0) / period;
  ema.push(sma);

  // Calculate EMA for remaining prices
  for (let i = period; i < prices.length; i++) {
    ema.push((prices[i] - ema[ema.length - 1]) * multiplier + ema[ema.length - 1]);
  }

  return ema;
}

export function calculateRSI(prices: number[], period: number = 14): number[] {
  const rsi = [];
  let gains = [];
  let losses = [];

  // Calculate price changes
  for (let i = 1; i < prices.length; i++) {
    const change = prices[i] - prices[i - 1];
    gains.push(change > 0 ? change : 0);
    losses.push(change < 0 ? -change : 0);
  }

  // Calculate initial averages
  let avgGain = gains.slice(0, period).reduce((sum, gain) => sum + gain, 0) / period;
  let avgLoss = losses.slice(0, period).reduce((sum, loss) => sum + loss, 0) / period;

  // Calculate RSI values
  for (let i = period; i < prices.length; i++) {
    avgGain = (avgGain * (period - 1) + gains[i - 1]) / period;
    avgLoss = (avgLoss * (period - 1) + losses[i - 1]) / period;

    const rs = avgGain / avgLoss;
    rsi.push(100 - (100 / (1 + rs)));
  }

  return rsi;
}

export function calculateATR(data: OHLCV[], period: number = 14): number[] {
  const tr = [];
  const atr = [];

  // Calculate True Range
  for (let i = 0; i < data.length; i++) {
    if (i === 0) {
      tr.push(data[i].high - data[i].low);
      continue;
    }

    const trueHigh = Math.max(data[i].high, data[i - 1].close);
    const trueLow = Math.min(data[i].low, data[i - 1].close);
    tr.push(trueHigh - trueLow);
  }

  // Calculate ATR
  let sum = tr.slice(0, period).reduce((a, b) => a + b, 0);
  atr.push(sum / period);

  for (let i = period; i < tr.length; i++) {
    atr.push((atr[atr.length - 1] * (period - 1) + tr[i]) / period);
  }

  return atr;
}

export function calculateMACD(prices: number[], fastPeriod = 12, slowPeriod = 26, signalPeriod = 9) {
  const fastEMA = calculateEMA(prices, fastPeriod);
  const slowEMA = calculateEMA(prices, slowPeriod);
  const macdLine = fastEMA.map((fast, i) => fast - slowEMA[i]);
  const signalLine = calculateEMA(macdLine, signalPeriod);
  const histogram = macdLine.map((macd, i) => macd - signalLine[i]);

  return {
    macd: macdLine,
    signal: signalLine,
    histogram
  };
}

export function evaluateSignal(data: OHLCV[], indicators: TechnicalIndicators, criteria: SignalCriteria): boolean {
  let signal = true;

  if (criteria.minRSI !== undefined && indicators.rsi14 < criteria.minRSI) signal = false;
  if (criteria.maxRSI !== undefined && indicators.rsi14 > criteria.maxRSI) signal = false;
  
  if (criteria.minVolume !== undefined && indicators.volume < criteria.minVolume) signal = false;
  if (criteria.volumeMultiplier !== undefined && 
      indicators.volume < indicators.volumeSMA20 * criteria.volumeMultiplier) signal = false;

  if (criteria.trendEMA === 'above' && data[data.length - 1].close < indicators.ema20) signal = false;
  if (criteria.trendEMA === 'below' && data[data.length - 1].close > indicators.ema20) signal = false;

  if (criteria.macdCrossover && 
      !(indicators.macd.histogram > 0 && indicators.macd.histogram > indicators.macd.signal)) signal = false;

  if (criteria.atrMultiplier !== undefined) {
    const volatility = indicators.atr14 / data[data.length - 1].close;
    if (volatility > criteria.atrMultiplier) signal = false;
  }

  return signal;
}

export async function fetchHistoricalData(symbol: string, interval: string = '1m'): Promise<OHLCV[]> {
  // TODO: Implement real data fetching from broker/market data API
  // For now, return mock data
  const now = Date.now();
  const mockData: OHLCV[] = [];
  
  for (let i = 0; i < 100; i++) {
    const basePrice = 100;
    const timestamp = now - (i * 60 * 1000); // 1-minute intervals
    mockData.unshift({
      timestamp,
      open: basePrice + Math.random() * 10,
      high: basePrice + Math.random() * 15,
      low: basePrice + Math.random() * 5,
      close: basePrice + Math.random() * 10,
      volume: Math.floor(Math.random() * 10000)
    });
  }
  
  return mockData;
}
