"use client";

import React from 'react';
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Alert } from "@/components/ui/alert";
import {
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart";
import { LineChart, Line, ResponsiveContainer } from 'recharts';

interface StockData {
  symbol: string;
  momentum: number;
  ema9: number;
  ema20: number;
  rsi: number;
  volume: number;
  avgVolume: number;
  currentPrice: number;
  target: number;
  stopLoss: number;
  priceHistory: { time: string; price: number }[];
}

export default function FNODashboard() {
  const [stocks, setStocks] = React.useState<StockData[]>([]);
  const [loading, setLoading] = React.useState(true);

  // Mock data - Replace with real API integration
  React.useEffect(() => {
    const mockData: StockData[] = [
      {
        symbol: "NIFTY",
        momentum: 6.2,
        ema9: 19250,
        ema20: 19100,
        rsi: 65,
        volume: 1500000,
        avgVolume: 1000000,
        currentPrice: 19300,
        target: 19650,
        stopLoss: 19100,
        priceHistory: [
          { time: "9:15", price: 19200 },
          { time: "10:15", price: 19250 },
          { time: "11:15", price: 19300 },
          { time: "12:15", price: 19280 },
          { time: "13:15", price: 19320 },
        ]
      }
    ];

    setStocks(mockData);
    setLoading(false);
  }, []);

  const getSignalColor = (momentum: number): string => {
    if (momentum >= 5) return "bg-green-500";
    if (momentum >= 3) return "bg-yellow-500";
    return "bg-red-500";
  };

  if (loading) {
    return <div className="flex justify-center items-center h-96">Loading...</div>;
  }

  const chartConfig = {
    price: {
      label: "Price",
      color: "#0ea5e9",
    },
  };

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {stocks.map((stock) => (
          <Card key={stock.symbol} className="p-4">
            <div className="flex justify-between items-start mb-4">
              <div>
                <h3 className="text-lg font-bold">{stock.symbol}</h3>
                <Badge className={getSignalColor(stock.momentum)}>
                  {stock.momentum}% Momentum
                </Badge>
              </div>
              <Button variant="outline" size="sm">
                Trade
              </Button>
            </div>

            <div className="h-[200px] mb-4">
              <ChartContainer config={chartConfig}>
                <ResponsiveContainer width="100%" height={200}>
                  <LineChart data={stock.priceHistory}>
                    <Line
                      type="monotone"
                      dataKey="price"
                      stroke="var(--color-price)"
                      strokeWidth={2}
                      dot={false}
                    />
                    <ChartTooltip
                      content={({ active, payload }) => (
                        <ChartTooltipContent
                          active={active}
                          payload={payload}
                          labelKey="time"
                        />
                      )}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </ChartContainer>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between">
                <span>EMA9/20 Cross:</span>
                <Badge variant={stock.ema9 > stock.ema20 ? "default" : "destructive"}>
                  {stock.ema9 > stock.ema20 ? "Bullish" : "Bearish"}
                </Badge>
              </div>

              <div className="flex justify-between">
                <span>RSI(14):</span>
                <span className={stock.rsi > 70 ? "text-red-500" : stock.rsi < 30 ? "text-green-500" : ""}>
                  {stock.rsi}
                </span>
              </div>

              <div className="flex justify-between">
                <span>Volume Spike:</span>
                <Badge variant={stock.volume > stock.avgVolume * 1.5 ? "default" : "secondary"}>
                  {((stock.volume / stock.avgVolume) * 100).toFixed(0)}%
                </Badge>
              </div>

              <div className="mt-4 p-2 bg-gray-50 rounded-md">
                <div className="flex justify-between text-sm">
                  <span>Target: {stock.target}</span>
                  <span>Stop Loss: {stock.stopLoss}</span>
                </div>
              </div>
            </div>
          </Card>
        ))}
      </div>

      {stocks.length === 0 && (
        <Alert>
          <p>No stocks matching the momentum criteria found.</p>
        </Alert>
      )}
    </div>
  );
}
